/*!* Variables CSS *!*/
:root{
    --font-logo: Mayonice, Arial, Helvetica, sans-serif;
    --font-title: Louis George Cafe, Arial, Helvetica, sans-serif;
    --font-text: latoregular, Arial, Helvetica, sans-serif;
    --font-size-nav-logo : 4.8rem;
    --font-size-nav-h2 : 2rem;
    --font-size-body : 1.6rem;
    --color-text-main: #141414;
    --color-text-secondary: #FFFFFF;
    --color-dark-grey: #D9D9D9;
}

/* ---IDIOMATIC CSS--- */
/*  Positioning (position, z-index, top, right, bottom, left...)
    Display & Box Model (display, overflow, box-sizing, width, height, padding, border, margin...)
    Other (background, color, font-family, font-size, text-align...)
*/
/* ---modif font-size du html pour utiliser rem en base 10--- */
html {
    font-size: 62.5%;
}

body {
    background-image:
        url("/public/assets/images/round-high-left.png"),
        url("/public/assets/images/round-high-right.png");
    background-repeat: no-repeat, no-repeat;
    background-position: left -100px , right -100px ;
    background-size: 40%, 30%;
    font-family: var(--font-text);
    font-size: var(--font-size-body);
    line-height: 150%;
    color: var(--color-text-main);
}

section {
    padding-inline: 10%;
}
